@import "fonts";
@import "libs/normalize";
@import "libs/vars";

body {
    background-color: #fff;
    font-family: "Roboto";
    color: #111827;
}

._container {
    max-width: 1400px;
    padding: 0px 15px;
    margin: 0 auto;

    @media (max-width: $md1) {
        position: absolute;
        top: 100px;
        width: 100%;
    }
}

#root {
    scrollbar-width: none;
}

body::-webkit-scrollbar {
    width: 0; /* Для Chrome, Safari и Opera */
}

// UI
@import "components/UI/title";
@import "components/UI/loader";
@import "components/UI/underline-anim";

// components
@import "components/header";
@import "components/nav-header";
@import "components/burger";

@import "components/FAQ/accordion-faq";
@import "components/FAQ/faq";

@import "components/article";

@import "components/elretn";
@import "components/jugendlichen";
