.burger-menu-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;

    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    padding-right: 30px;

    transform: translateY(0%);

    transition: 0.25s ease-in-out all;

    &.hide {
        transform: translateY(-100%);
    }

    @media (max-width: $md1) {
        display: flex;
    }
}

.burger-icon {
    cursor: pointer;

    display: block;
}

.bar {
    display: block;
    width: 30px;
    height: 3px;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
}

#burger-menu-toggle {
    display: none;
}

.burger-menu {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    z-index: 999;

    transition: 0.25s ease-in-out all;

    transform: translateY(-200%);

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 900;
    &__nav {
        text-align: center;
    }

    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    &__item {
        margin: 10px 0;
    }

    &__link {
        text-decoration: none;
        color: black;
        font-size: 21px;
        font-weight: 500;
        padding: 10px;
        display: inline-block;

        &.active {
            text-decoration: underline;
        }
    }

    &__logo {
        margin-bottom: 50px;
    }

    &__contact-btn {
        margin-top: 50px;
    }
}

#burger-menu-toggle:checked + .burger-icon .bar:nth-child(1) {
    transform: rotate(-45deg) translate(-6px, 6px);
}

#burger-menu-toggle:checked + .burger-icon .bar:nth-child(2) {
    opacity: 0;
}

#burger-menu-toggle:checked + .burger-icon .bar:nth-child(3) {
    transform: rotate(48deg) translate(-6px, -6.5px);
}

.burger-menu._active {
    transform: translateY(0%);
}
