.header {
    margin-top: 15px;
    margin-bottom: 32px;

    border-radius: 70px;
    background: #fff;
    display: block;

    @media (max-width: $md1) {
        display: none;
    }
    // .header__content
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    // .header__logo
    &__logo {
        color: #1f1f1f;
        font-size: 24px;
        font-weight: 700;
        line-height: 120%; /* 28.8px */

        @media (max-width: $md2) {
            font-size: 21px;
        }
    }
    &__logo-container {
        display: flex;
        align-items: center;
        gap: 25px;
    }
    &__logo-img {
        width: 150px;
        height: 150px;

        @media (max-width: $md2) {
            width: 100px;
            height: 100px;
        }
    }
    // .header__nav
    &__nav {
        border-radius: 40px;
        background: #fefeff;
        padding: 20px;
    }
    // .header__contact-btn
    &__contact-btn {
    }
}
