.underline-anim {
    display: inline-block;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: -3px;
        left: 0;
        background-color: $blue; /* Цвет линии */
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out;
    }

    @media (any-hover: hover) {
        &:hover::after {
            visibility: visible;
            transform: scaleX(1);
        }
    }
}
