.szh-accordion {
    display: flex;
    flex-direction: column;
    gap: 15px;

    &__item {
        img {
            transition: 0.25s ease-in-out all;
        }
    }
    // .szh-accordion__item-heading
    &__item-heading {
    }
    // .szh-accordion__item-btn
    &__item-btn {
        width: 100%;

        display: flex;
        justify-content: space-between;
        align-items: center;

        background-color: $blue;
        color: #fff;
        border-radius: 10px;
        text-align: start;
        padding: 15px 25px;
        border: none;

        cursor: pointer;
        font-size: 18px;

        margin-bottom: 15px;

        @media (max-width: $md2) {
            margin-bottom: 8px;
        }
    }
    // .szh-accordion__item-content
    &__item-content {
        transition: height 0.25s cubic-bezier(0, 0, 0, 1);

        font-size: 18px;
        padding-left: 15px;
    }
    // .szh-accordion__item-panel
    &__item-panel {
    }
}

.szh-accordion__item--expanded {
    img {
        transform: rotate(180deg);
    }
}
