.article {
    &__content {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &__item {
    }
    &__title {
        font-size: 25px;
        font-weight: 500;
        padding-bottom: 8px;

        @media (max-width: $md2) {
            font-size: 20px;
        }
    }

    &__text {
        font-size: 18px;

        @media (max-width: $md2) {
            font-size: 17px;
        }
    }
}
